@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins';
    font-style: normal;
}

body {
    overflow-x: scroll;
}

.navbar {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
    position: fixed;
    top: 0;
    left: 300px;
    width: 80%;
    background-color: #ffff;
    z-index: 40000000000;
}

.btn-003 {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #FE8700;
    background-color: white;
    border: 1px solid #fe8700;
    border-radius: 25px;
    height: 44px;
    width: 231px;
    margin-top: 7px;

}

.btn-003:hover {
    background-color: #FFF7EE;
    color: #FE8700;
}

