.permis-UL .permis-LI {
    color: #AAAAAA;
    display: block;
    position: relative;
    float: left;
    width: 100%;
    height: 100px;
}

.permis-UL .permis-LI input[type=radio] {
    position: absolute;
    visibility: hidden;
}

.permis-UL .permis-LI label {
    display: block;
    position: relative;
    font-weight: 300;
    font-size: 1.35em;
    font-size: 1.35em;
    font-size: 15px;
    margin-left: 1px;
    z-index: 9;
    cursor: pointer;
    -webkit-transition: all 0.25s linear;
    background: #CCCCCC;
    border-radius: 100px;
    border: 0px;
    width: 65px;
}

.permis-UL .permis-LI label p {
    margin-left: 30px;
    font-size: 14px;
}

.permis-UL .permis-LI:hover label {
    color: black;

}

.permis-UL .permis-LI .check {
    display: block;
    position: absolute;
    border: 5px solid #ffff;
    border-radius: 100%;
    height: 20px;
    width: 20px;
    top: 3px;
    left: 4px;
    z-index: 100000;
    transition: border .25s linear;
    -webkit-transition: border .25s linear;

}

.permis-UL .permis-LI:hover .check {
    /* border: 5px soLId black; */
}

.permis-UL .permis-LI .check::before {
    display: block;
    position: absolute;
    content: '';
    border-radius: 100%;
    height: 12px;
    width: 12px;
    top: -1px;
    left: -1px;
    background: white;
    margin: auto;
    transition: background 0.25s linear;
    -webkit-transition: background 0.25s linear;
}

.permis-LI input[type=radio]:checked~.check {
    border: 5px solid #ffff;
    z-index: 9999;
    left: 4px;
    top: 3px;
}

.permis-LI input[type=radio]:checked~.check::before {
    background: #FE8700;
}

.permis-LI input[type=radio]:checked~label {
    color: #ffff;
    background: #FE8700;
    border-radius: 100px;
    border: 0px;
    width: 65px;
    height: 25px;
}

