/* Emoji css */

.ul-Styling {
    display: flex;
    justify-content: space-around;
    list-style: none;
    padding-left: 0px;
    position: relative;
    right: 35px;
}

.ul-Styling li {
    margin-inline: -30px;
}

.react {
    --r: 40px;
    display: inline-grid;
    cursor: pointer;
    width: var(--r);
    height: var(--r);
    border-radius: 50%;
    line-height: var(--r);
    text-align: center;
    transform: rotate(0);
}

.react i:before {
    content: attr(data-icon);
    grid-area: 1/1;
    font-style: normal;
    font-size: calc(0.6*var(--r));
}

.react i {
    filter: grayscale(100%);
    transform: scale(1);
    grid-area: 1/1;
    display: inherit;
    transition: transform .6s cubic-bezier(0.5, -12, 1, -12);
}

.react:hover {
    transform: rotate(0.1deg);
    transition: transform 0.25s cubic-bezier(0.5, 400, 0.5, -400);
}

.react:hover p {
    transform: none !important;
    transition: none !important;
}

.react:before {
    content: "";
    grid-area: 1/1;
    border-radius: 50%;
    border: solid orange calc(var(--r)/2);
    transform: scale(0);
}

.react:after,
.react i:after {
    grid-area: 1/1;
    margin: calc(var(--r)/-2);
    --c1: radial-gradient(red 50%, #0000 60%);
    --c2: radial-gradient(orange 50%, #0000 60%);
    background:
        var(--c1), var(--c1), var(--c1), var(--c1),
        var(--c2), var(--c2), var(--c2), var(--c2);
    background-size: calc(var(--r)/6) calc(var(--r)/6);
    background-position:
        calc(50% - var(--r)/2) calc(50% - var(--r)/2),
        calc(50% + var(--r)/2) calc(50% - var(--r)/2),
        calc(50% - var(--r)/2) calc(50% + var(--r)/2),
        calc(50% + var(--r)/2) calc(50% + var(--r)/2),
        calc(50% + 0px) calc(50% + var(--r)*0.707),
        calc(50% + var(--r)*0.707) calc(50% + 0px),
        calc(50% - var(--r)*0.707) calc(50% + 0px),
        calc(50% + 0px) calc(50% - var(--r)*0.707);
    background-repeat: no-repeat;
    color: #0000 !important;
    transform: scale(0);
}

input:checked+.react+p {
    color: black;
    font-weight: bolder;
}

.react i:after {
    background-size: calc(var(--r)/8) calc(var(--r)/8);
    transform: rotate(60deg) scale(0);
}

